body {
  line-height: 40px;
  font-size: 1.8em;
  font-family: 'Teko', sans-serif;
  background-image: linear-gradient( #b3cde0, #03396c);
}

.container {
  max-width: 1000px;
  text-align: center;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin: 0 auto;
  color: #005b96;
}

.header h1 {
  font-family: 'Pacifico', cursive;
  font-size: 3em;
}
@media (max-width: 800px) {
  .header h1 {
    font-size: 2em;
    line-height: 70px;
  }
}


.form {
  text-align: center;
  color: white;
}

.form .field {
  padding: 0;
  margin: 6px;
  font-size: .6em;
  border: 0;
  border-radius: 10px;
  padding: .4em;
  background: #0c0c0c;
  color: white;
}

.form h3 {
  text-align: center;
}

@media (max-width: 800px) {
  .form h3 {
   font-size: .8em;
  }
}

button {
  margin: 3px;
  font-size: .6em;
  border: 0;
  border-radius: 10px;
  padding: .4em;
  background: #0c0c0c;
  color: white;
}

.smurfrow {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.asmurf {
  width: 20%;
  border-radius: 1em;
  margin: 5px;
  background: white;
  padding: 2em;
  -webkit-box-shadow: 1px 0 14px -6px rgba(0, 0, 0, 1);
  -moz-box-shadow: 1px 0 14px -6px rgba(0, 0, 0, 1);
  box-shadow: 1px 0 14px -6px rgba(0, 0, 0, 1);
}

@media (max-width: 800px) {
  .asmurf{
   width: 100%;
   padding: 1em;
  }
}

.footer {
  text-align: center;
  background: black;
  font-size: 20px;
  padding: 5em;
  color: white;
}

.footer a{
  text-decoration: none;
  color: rgb(230, 217, 104); 
}
